import { useEffect, useRef } from "react"
import { gsap } from "gsap"
import { ScrollTrigger } from "gsap/ScrollTrigger"

gsap.registerPlugin(ScrollTrigger)

const Moon = ({ moonColor }) => {
    const moonRef = useRef(null)

    useEffect(() => {
        const w = window.innerWidth + 100
        let timeline = gsap.timeline({
            scrollTrigger: {
                trigger: moonRef.current,
                start: 'top center',
                end: 'bottom -180%',
                scrub: 1,
                pin: true,
                anticipatePin: 0.5,
                pinSpacing: false,
            }
        })

        timeline
            .fromTo(moonRef.current, { x: -100, y: 0 }, { x: w, y: 150, rotate: -90 })

        return () => {
            timeline?.kill()
            timeline.scrollTrigger?.kill()
        }
    }, [])

    return (
        <div className="moon-box">
            <div className='moon' ref={moonRef} style={{...moonColor}}></div>
        </div>
        // <div className="moon-box">
        //     <svg ref={moonRef} version="1.1" id="moon" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100" fill={moonColor}>

        //         <filter id="shadow" y="-30%" x="-45%" width="160%" height="160%">
        //             <feDropShadow dx="0" dy="0" stdDeviation="8" floodOpacity="0.8" floodColor={moonColor} />
        //         </filter>

        //         <path d="M50.6,49.6c0-13.6,7.1-25.1,16.8-28.6c-2.1-0.5-4.3-0.7-6.6-0.7c-16.4,0-29.7,13.3-29.7,29.7s13.3,29.7,29.7,29.7 c2.7,0,5.4-0.4,7.9-1.1C58.4,75.7,50.6,63.8,50.6,49.6z" filter="url(#shadow)"/>
        //     </svg>
        // </div>
    )
}

export default Moon
