import gsap from "gsap"
import { ScrollTrigger } from "gsap/ScrollTrigger"
import { useRef, useEffect } from "react"
import present1 from '../images/birthday-wrapped-present.svg'
import present2 from '../images/present-gift.svg'

gsap.registerPlugin(ScrollTrigger)

const Window = ({fromColor, toColor}) => {

    const divRef = useRef(null)

    useEffect(() => {
        let timeline = gsap.timeline({
            scrollTrigger: {
                trigger: divRef.current,
                scrub: 0.25,
                start: 'top top',
            }
        })

        timeline
            .fromTo(divRef.current, {background: fromColor}, {background: toColor})
            .to(divRef.current, {background: toColor})

        return () => {
            timeline?.kill()
            timeline.scrollTrigger?.kill()
        }
    }, [])

    return (
        <div className='window' ref={divRef} style={{height: '200vh'}}>
            <div className="presents">
                <img style={{width: '40vw'}} src={present2} alt="" />
                <img style={{width: '40vw'}} src={present1} alt="" />
            </div>
        </div>
    )
}

export default Window
