import { hot } from 'react-hot-loader/root'
import './styles/index.scss'

import { useEffect, useState } from 'react';
import Window from './components/Window';
import Message from './components/Message';
import Stars from './components/Stars';
import Moon from './components/Moon';
import Sun from './components/Sun';
import ScrollDown from './components/ScrollDown';
import Cloud from './components/Cloud';
import Birthday from './components/Birthday';
import Baloon from './components/Baloon';

function App() {
    const [visible, setVisible] = useState(false)

    useEffect(() => {
        const timer = setTimeout(() => setVisible(true), 1000)
        return () => {
            clearTimeout(timer)
        }
    }, [])

    const w = window.innerWidth + 200
    const messageAnimation = {
        fromLeft: [
            {x: -w, autoAlpha: 0}, {duration: 1, x: 0, autoAlpha: 1, ease: 'elastic.out(0.8, 0.5)'}
        ],
        fromRight: [
            {x: w, rotate: -180, autoAlpha: 0}, {duration: 1, x: 0, rotate: 0, autoAlpha: 1, ease: 'back.out(2)'}
        ],
        fromScale: [
            {scale: 0, rotate: 180, autoAlpha: 0}, {duration: 1, scale: 1, rotate: 0, autoAlpha: 1, ease: 'back.out(2)'}
        ],
    }

    const starsColors = {
        night2: {
            background: 'rgb(18, 29, 59)',
            boxShadow: '0px 0px 15px rgba(133, 10, 84, 1.1)'
        },
        night3: {
            background: 'rgb(255, 255, 255)',
            boxShadow: '0px 0px 15px rgba(255, 255, 255, 1.1)'
        }
    }

    const moonColor = {
        night2: {
            boxShadow: '-20px -10px 0px rgb(18, 29, 59)',
            filter: 'drop-shadow(0px 0px 10px rgb(133, 10, 84))'
        },
        night3: {
            boxShadow: '-20px -10px 0px rgb(255, 255, 255)',
            filter: 'drop-shadow(0px 0px 10px rgb(255, 255, 255))'
        }
    }

    const sunColor = {
        day2: {
            boxShadow: '0px 0px 20px rgb(255, 255, 43),' +
            '2px 2px 20px rgb(255, 255, 43),' + '-2px -2px 20px rgb(255, 255, 43)',
            background: 'rgb(255, 255, 43)'
        }
    }

    return (
        <div className={visible ? 'app visible' : 'app'}>
            {/* <div className='down'></div> */}
            <ScrollDown />
            <Window fromColor='rgb(10,10,10)' toColor='rgb(255,255,255)'/>
            <section className="night" style={{background: 'linear-gradient(rgb(255,255,255), rgb(200,200,200))'}}>
                <Moon />
                <Stars />
                <Message textColor='hsl(224, 5%, 23%)' animation={messageAnimation.fromLeft} message='Ночь настала?' />
            </section>
            <Window fromColor='rgb(200,200,200)' toColor='rgb(4, 10, 26)'/>
            <section className='day' style={{background: 'rgb(4, 10, 26)'}} >
                <Message textColor='hsl(224, 5%, 100%)' animation={messageAnimation.fromRight} message='А это день?'/>
                <Sun />
                <Cloud cloudColor='rgb(220, 220, 220)' />
            </section>
            <Window fromColor='rgb(4, 10, 26)' toColor='rgb(255,255,255)'/>
            <section className="night" style={{background: 'rgb(255,255,255)'}} >
                <Moon moonColor={moonColor.night2}/>
                <Stars starsColor={starsColors.night2} />
                <Message textColor='hsl(342, 50%, 30%)' animation={messageAnimation.fromScale} message='Другая ночь?'/>
            </section>
            <Window fromColor='rgb(255,255,255)' toColor='rgb(254, 255, 235)'/>
            <section className='day' style={{background: 'rgb(254, 255, 235)'}} >
                <Message textColor='hsl(224, 5%, 30%)' animation={messageAnimation.fromRight} message='А может всё должно быть так?'/>
            </section>
            <Window fromColor='rgb(254, 255, 235)' toColor='rgb(65, 109, 255)'/>
            <section className='day' style={{background: 'rgb(65, 109, 255)'}} >
                <Message animation={messageAnimation.fromScale} textColor='hsl(224, 5%, 100%)' message='Вот день'/>
                <Sun sunColor={sunColor.day2} />
                <Cloud cloudColor='rgb(255, 255, 255)' />
            </section>
            <Window fromColor='rgb(65, 109, 255)' toColor='rgb(30, 30, 30)'/>
            <section className="night" style={{background: 'rgb(30, 30, 30)'}} >
                <Moon moonColor={moonColor.night3} />
                <Stars starsColor={starsColors.night3} />
                <Message textColor='hsl(224, 5%, 90%)' animation={messageAnimation.fromLeft} message='Вот ночь' />
            </section>
            <Window fromColor='rgb(30, 30, 30)' toColor='rgb(201, 65, 87)'/>
            <section className='day' style={{background: 'rgb(201, 65, 87)'}} >
                <Message animation={messageAnimation.fromScale} textColor='hsl(224, 5%, 100%)' message='Пусть всё будет так, как хочешь'/>
                {/* <Birthday /> */}
            </section>
            <section className='day' style={{background: 'linear-gradient(rgb(201, 65, 87), rgb(112, 19, 34))', height: '110vh'}} >
                <Birthday />
                <Baloon />
            </section>
        </div>
    )
}
export default hot(App);
