import gsap from "gsap"
import { ScrollTrigger } from "gsap/ScrollTrigger"
import { useEffect, useRef } from "react"

gsap.registerPlugin(ScrollTrigger)

const Birthday = () => {
    const wordsRef = useRef(null)
    const letterRef = useRef([])

    const spaned = []

    const message = 'С днём Рождения!'
    const letters = message.split('')
    letterRef.current = []

    const addToRef = (el) => {
        if (el && !letterRef.current.includes(el)) {
            letterRef.current.push(el)
        }
    }

    letters.forEach((letter, i) => {
        spaned.push(<div ref={addToRef} key={i}>{letter}</div>)
    })

    useEffect(() => {
        let timeline = gsap.timeline({
            scrollTrigger: {
                trigger: wordsRef.current,
                // markers: true,
                start: 'top center',
                end: 'top top+=150',
                toggleActions: 'play none reverse none'
            }
        })

        timeline
            .fromTo(letterRef.current, {y: -20, scale: 0, rotate: -90}, {duration: 0.2, rotate: 0, y: 0, scale: 1, stagger: 0.1})

        return () => {
            timeline?.kill()
            timeline.scrollTrigger?.kill()
        }

    }, [])

    return (
        <div className='hb-message' ref={wordsRef}>
            {spaned}
        </div>
    )
}

export default Birthday
