import gsap from "gsap"
import { ScrollTrigger } from "gsap/ScrollTrigger"

import { useEffect, useRef } from "react"

gsap.registerPlugin(ScrollTrigger)

const Stars = ({ starsColor }) => {
    const boxRef = useRef(null)
    const starsRef = useRef([])

    const starsNum = 15
    const starsArr = []

    starsRef.current = []

    const addToRef = (el) => {
        if (el && !starsRef.current.includes(el)) {
            starsRef.current.push(el)
        }
    }

    const getRandom = (min, max) => {
        return Math.random() * (max - min) + min
    }

    for (let i = 0; i < starsNum; i++) {
        let randomX = getRandom(10, innerWidth - 20)
        let randomY = getRandom(10, innerHeight - 50)
        let randSize = getRandom(2, 8)
        const style = {
            left: `${randomX}px`,
            top: `${randomY}px`,
            width: `${randSize}px`,
            height: `${randSize}px`,
        }
        starsArr.push(<div className='star' style={{...style, ...starsColor}} ref={addToRef} key={i} ></div>)
    }

    useEffect(() => {
        let boxTimeline = gsap.timeline({
            scrollTrigger: {
                trigger: boxRef.current,
                pin:true,
                anticipatePin: 1,
                pinSpacing: false,
                start: 'top top'
            }
        })

        starsRef.current.forEach((el, ind) => {
            let timeline = gsap.timeline({
                scrollTrigger: {
                    trigger: el,
                    start: 'top center',
                    toggleActions: 'play none reverse none',
                    // pinSpacing: false,
                    // pin: true,
                    // markers: true
                }
            })

            timeline
                .fromTo(el, {scale: 0, autoAlpha: 0}, {duration: 0.8, autoAlpha: 1, scale: 1, ease: "back.out(6)"})

            return () => {
                timeline?.kill()
                timeline.scrollTrigger?.kill()
            }
        })

        return () => {
            boxTimeline?.kill()
            boxTimeline.scrollTrigger?.kill()
        }

    }, [])

    return (
        <div ref={boxRef} className='stars-box'>
            {starsArr}
        </div>
    )
}

export default Stars
